<template>
  <div style="margin: 0px 15px;">
    <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">å…¨é€‰</el-checkbox>
    <div style="margin: 15px 0;"></div>
    <el-checkbox-group v-model="checkedGoods" @change="handleCheckedGoodsChange" class="vertical-checkbox-group">
      <el-checkbox v-for="good in localGoods" :label="good.id" :key="`${good.id}-${good.name}`" style="height: inherit;" border>
        <el-card :body-style="{ padding: '0px' }" shadow="hover">
          <img :src="good.image" class="image"> <!-- åŠ¨æ€ç»‘å®šå•†å“å›¾ç‰‡ -->
          <div style="padding: 14px;">
            <span>{{ good.name }} - Â¥{{ good.price }}</span> <!-- åŠ¨æ€ç»‘å®šå•†å“åç§°å’Œä»·æ ¼ -->
            <div class="bottom clearfix">
              <el-button type="danger" class="button" style="margin-left:200px" @click="delete_good(good.id)">åˆ é™¤</el-button>
            </div>
          </div>
        </el-card>
      </el-checkbox>
    </el-checkbox-group>
    <!-- æ–°å¢ï¼šä»˜æ¬¾æŒ‰é’® -->
    <div style="text-align: center; margin-top: 20px;">
      <el-button type="primary" @click="payFromCart">ä»˜æ¬¾</el-button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    goods: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      checkAll: false,
      checkedGoods: [],
      isIndeterminate: true,
      localGoods: [], // ç”¨äºå­˜å‚¨ goods çš„å‰¯æœ¬
    };
  },
  watch: {
    goods: {
      immediate: true,
      handler(newVal) {
        this.localGoods = [...newVal]; // ç›‘å¬ goods å˜åŒ–å¹¶æ›´æ–°å‰¯æœ¬
      },
    },
  },
  methods: {
    delete_good(id) {
      this.localGoods = this.localGoods.filter(good => good.id !== id); // ä¿®æ”¹å‰¯æœ¬è€Œé props
      this.$emit('update:goods', this.localGoods); // é€šçŸ¥çˆ¶ç»„ä»¶æ›´æ–° goods
    },
    handleCheckAllChange(val) {
      this.checkedGoods = val ? this.localGoods.map(good => good.id) : [];
      this.isIndeterminate = false;
    },
    handleCheckedGoodsChange() {
      // åˆ é™¤æœªä½¿ç”¨çš„å˜é‡ value
      const checkedCount = this.checkedGoods.length; // ç›´æ¥ä½¿ç”¨ this.checkedGoods.length
      this.checkAll = checkedCount === this.localGoods.length;
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.localGoods.length;
    },
    // æ–°å¢ï¼šä»˜æ¬¾é€»è¾‘
    payFromCart() {
      const selectedGoods = this.localGoods.filter(good => this.checkedGoods.includes(good.id));
      const totalAmount = selectedGoods.reduce((sum, good) => sum + good.price, 0);

      if (totalAmount === 0) {
        this.$message.warning('è¯·é€‰æ‹©è¦è´­ä¹°çš„å•†å“');
        return;
      }

      this.$prompt('è¯·è¾“å…¥ä¿¡ç”¨å¡å·', 'æ”¯ä»˜', {
        confirmButtonText: 'ç¡®è®¤æ”¯ä»˜',
        cancelButtonText: 'å–æ¶ˆ',
        inputPattern: /^\d{16}$/, // éªŒè¯16ä½æ•°å­—
        inputErrorMessage: 'è¯·è¾“å…¥æœ‰æ•ˆçš„16ä½ä¿¡ç”¨å¡å·',
      }).then(() => {
        // æ¨¡æ‹Ÿè°ƒç”¨é“¶è¡Œæ”¯ä»˜ç³»ç»Ÿ
        this.$message.success(`æ”¯ä»˜æˆåŠŸï¼Œå·²æ‰£é™¤ä½™é¢ Â¥${totalAmount.toFixed(2)}`);
        // æ¸…ç©ºè´­ç‰©è½¦ä¸­çš„å·²é€‰å•†å“
        this.localGoods = this.localGoods.filter(good => !this.checkedGoods.includes(good.id));
        this.checkedGoods = []; // æ¸…ç©ºé€‰ä¸­å•†å“åˆ—è¡¨
        this.$emit('update:goods', this.localGoods); // é€šçŸ¥çˆ¶ç»„ä»¶æ›´æ–° goods

        // é€šçŸ¥ç®¡ç†å‘˜æ›´æ–°é”€å”®æ•°æ®
        this.$emit('payment-complete', selectedGoods, totalAmount);
      }).catch(() => {
        this.$message.info('å·²å–æ¶ˆæ”¯ä»˜');
      });
    },
  },
};
</script>

<style scoped>
.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}

.button {
  padding: 0;
  float: right;
}

.vertical-checkbox-group {
  display: flex;
  flex-direction: column;
}

